import * as THREE from 'three';

import SceneSubject from './SceneSubject';
import GeneralLights from './GeneralLights';
import * as dat from 'dat.gui';

import { OBJLoader } from '../../threejs/examples/jsm/loaders/OBJLoader.js';
//const OBJLoader  = require('../../threejs/examples/jsm/loaders/OBJLoader.js')(THREE);

const OrbitControls = require('three-orbit-controls')(THREE);
//import {ObjectLoader} from 'three';
//const STLLoader = require('three-stl-loader')(THREE);
//const OBJLoader = require('three-obj-loader')(THREE);
//const loader = require('../../js/OBJLoader.js');
//const loader = new OBJLoader(THREE);
//loader.setPath('assets/');

//import {OBJLoader} from 'three';

const three = THREE;
export default canvas => {

    const clock = new THREE.Clock();
    const origin = new THREE.Vector3(0,0,0);

    const screenDimensions = {
        width: canvas.width,
        height: canvas.height
    }

    const scene = buildScene();
    const renderer = buildRender(screenDimensions);
    const camera = buildCamera(screenDimensions);
    const sceneSubjects = createSceneSubjects(scene);

    const controls = new OrbitControls( camera, renderer.domElement );

    controls.target.set( 0, 1, 0 );
    controls.update();

    // light
    const lights = GeneralLights(scene);
    

    // objects
    var geo, localPlane, globalPlane, backgroundcube;
    // material
    var matColor, matReflective, matFlat, matWireframe;   
    // GUI
    var shading;

    function buildScene() {
        const scene = new THREE.Scene();
        scene.background = new THREE.Color("#000");
        return scene;
    }

    function buildRender({ width, height }) {
        const renderer = new THREE.WebGLRenderer({ canvas: canvas, antialias: true, alpha: true });
        const DPR = window.devicePixelRatio ? window.devicePixelRatio : 1;
        renderer.shadowMap.enabled = true;
        renderer.localClippingEnabled = false;
        renderer.setPixelRatio(DPR);
        renderer.setSize(width, height);

        return renderer;
    }

    // input function
    
    function loadModel() {
        }
    
        function onProgress( xhr ) {
            if ( xhr.lengthComputable ) {
                var percentComplete = xhr.loaded / xhr.total * 100;
                console.log( 'model ' + Math.round( percentComplete, 2 ) + '% downloaded' );
                }
        }

        function onError() {
            console.log( 'error!!' );
        }

    function buildCamera({ width, height }) {
        /*
        const fieldOfView = 36;
        const aspectRatio = window.innerWidth / window.innerHeight;
        const nearPlane = 0.25;
        const farPlane = 10000; 
        const camera = new THREE.PerspectiveCamera(fieldOfView, aspectRatio, nearPlane, farPlane);

        camera.position.set( 3, 1.3, 3 );
        */

        const camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 2000 );
        camera.position.set( 6, 6, 0 );
        
        return camera;
    }

    function createSceneSubjects(scene) {
        

        // subject

        const customContainer = document.getElementById('canvas-gui-container');
        var gui = new dat.GUI({ autoPlace: false }); 
        var localPlane = new THREE.Plane( new THREE.Vector3( 0, - 1, 0 ), 0.8 );
        var globalPlane = new THREE.Plane( new THREE.Vector3( - 1, -2, 0 ), 0.1 );

        // material
        matColor = new THREE.Color();
        matColor.setRGB( 0.9, 0.95, 1.0 );
        
        backgroundcube = new THREE.CubeTextureLoader()
                    .setPath( './assets/textures/cube/pisa/' )
                    .load( [ 'px.png', 'nx.png', 'py.png', 'ny.png', 'pz.png', 'nz.png' ] );
        matReflective = new THREE.MeshStandardMaterial( { color: matColor, metalness: 0.1, roughness:0.1, exposure: 1.0, debug: false, envMap: backgroundcube} );

        matFlat = new THREE.MeshPhongMaterial( { color: matColor, envMap: backgroundcube, refractionRatio: 0.985, reflectivity: 0.9, flatShading: true} );
        matFlat.envMap.mapping = THREE.CubeRefractionMapping;

        matWireframe = new THREE.MeshBasicMaterial( { color: matColor, wireframe: true } );

        // manager        
        const manager = new THREE.LoadingManager(loadModel);        
        manager.onProgress = function ( item, loaded, total ) {
            //console.log( item, loaded, total );
        };


        // execute input
        var loader = new OBJLoader(manager);
        // =================object will install into ./public not in ./src===================
        loader.load( './assets/rhinojs/model.obj', function ( obj ) {
            // Object
            obj.traverse( function ( child ) {
                if ( child.isMesh ) {
                    geo = child;
                    geo.material = matReflective;
                    //var ob = new THREE.Mesh(child,material);
                    geo.castShadow = true;
                    scene.add(geo);
                }               
            });

            // background     
            var ground = new THREE.Mesh(
                        new THREE.PlaneBufferGeometry( 40, 40, 1, 1 ),
                        new THREE.MeshPhongMaterial( { color: 0xa0adaf, shininess: 150 } )
            );

            ground.rotation.x = - Math.PI / 2; // rotates X/Y to X/Z
            ground.receiveShadow = true;

            
            scene.add( ground );

        }, onProgress, onError );

        //GUI
        var effectController = {
            newShading: "Reflective"
        }
        gui.add( effectController, 'newShading', [ "Wireframe", "Flat", "Reflective" ]).name('Shading').onChange( matEx );

         function matEx(){
            if (geo!==undefined){
                if (geo.isMesh){
                    if(effectController.newShading !== shading)
                    {
                        shading = effectController.newShading;
                        console.log(shading);
                        if (shading === 'Wireframe'){geo.material=matWireframe;}
                        else if (shading === 'Flat'){geo.material=matFlat;}
                        else if (shading === 'Reflective'){geo.material=matReflective;}
                        geo.material.needsUpdate = true;
                    }
                }
            }
        }

        
        customContainer.appendChild(gui.domElement);
    }
    

    function update() {
        lights.update();
        renderer.render(scene, camera);
    }

    function onWindowResize() {
        const { width, height } = canvas;
        
        screenDimensions.width = width;
        screenDimensions.height = height;

        camera.aspect = width / height;
        camera.updateProjectionMatrix();
        
        renderer.setSize(width, height);
    }

    return {
        update,
        onWindowResize
    }
}